@import url(../index.css);

.registration-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 65px); /* Nav bar height to fit gray background*/
    background-color: #F4F4F4;
  }
  
  .registration-box {
    background-color: white;
    padding: 50px;
    padding-left: 100px;
    padding-right: 100px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 400px;
    width: 100%;
  }
  
  .registration-title {
    font-size: 28px;
    margin-bottom: 30px;
  }
  
  .registration-info {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .registration-input {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #d6d6d6;
    border-radius: 4px;
  }
  
  .registration-input:focus {
    outline: none;
    border-color: #e33434;
  }
  
  .registration-button {
    padding: 12px;
    font-size: 16px;
    text-decoration: bold;
    color: white;
    background-color: #e33434;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    font-weight: bold;
    text-decoration: none;
  }
  
  .registration-button:hover {
    background-color: #d12f2f;
  }

  .status-popup {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #000000;
    color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    font-size: 14px;
    text-align: center;
  }

  .popup-close {
    background: transparent;
    border: none;
    font-size: 20px; 
    font-weight: bold;
    color: white; 
    cursor: pointer;
    position: absolute; 
    top: 10px; 
    right: 10px; 
    padding: 0;
  }
  
  .popup-close:hover {
    color: #ff0000; 
    transform: scale(1.1); /* Slightly enlarge the button on hover */
  }
  
  .popup-close:focus {
    outline: none;
  }
  
  
  